@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap');
:root{

    --primary1-light:#FAF7E0;
    --primary1:#FFEC3F;
    --primary3:#FEE500;
    --primary2-dark1:#1976D2;
    --primary2-dark2: #209BFF;
    --primary2-dark3:#3DBDFF;
    --primary2-light2:#F8F8F8;
}
.App {
  text-align: center;
  width: 100%;
}

#root{
  width:100%;
  font-family: 'Inter', sans-serif;
  height: 100%;
  position:relative;
}
.App-link {
  color: #61dafb;
}

/*NAV BAR*/
header{
  all:unset;
}
.nav{
  padding: 10px;
  display: flex;
  width:100%;
  box-sizing: border-box;
  height: 60px;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 0.000015px 3px 2px rgba(0, 0, 0, 0.234);
  z-index: 100;
  background-color: var(--primary1);
  position: sticky;
  top:0;
}
.responsiveNav{
  position:fixed;
  top:40px;
  z-index: 1;
  width:100%;
  background:var(--primary1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap:2rem;
}

.responsiveNav a{
  font-size: 3rem;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: 600;
  opacity: 0.6;
}
.responsiveNav a:hover{
  opacity:1;
}
.okform{
  padding-bottom: 50px;
  margin-bottom: 100px;
}

.navRight{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 200px;
}

.navRight .icon{
  display: none;

}
.nav-sns{
  margin-right: 30px;
  top: 38%;
  right:20%;
  font-size: 12px;
}

#commission{
  font-size: 12px;
  background-color: var(--primary3);
  padding:7px 10px;
  border-radius: 10px;
  box-shadow: 1px 2px 14px -7px rgba(0,0,0,0.57);
  -webkit-box-shadow: 1px 2px 10px -7px rgba(0,0,0,0.57);
  -moz-box-shadow: 1px 2px 14px -7px rgba(0,0,0,0.57);
  transition: 0.2s ease-in-out;
  opacity: 0.5;
}
#commission:hover{
  opacity: 1;
  transition: 0.2s ease-in-out;
}

#commission a{
  color:black;
  transition: 0.1s ease-in-out;

}
#commission a:hover{
  transition: 0.1s ease-in-out;

}
.wepo-logo{
  width: 100px;
  margin-left: 50px;
}

.nav .link{
  text-decoration: none;
  color: black;
  opacity:1;
  transition: 0.2s ease-in;
  padding: 10px;
}

.nav .linkContainer{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.inputField{
  width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

#demo-radio-buttons-group-label{
  margin:0px;
  padding:0px;
}
.radioN{
  margin-top: 20px;
}

.MuiFormGroup-root{
  margin:0px;
  padding:0px;
  font-size: 12px;

}
.form{
  margin: auto;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 80%;
}

.form h2{
  font-family: 'Fredoka';
  font-weight: 250;
}
/*HERO ================================================*/

.heroContainer{
  height:650px;
  padding: 10px 230px 0px 230px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  position:relative;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.heroLeft{
  width:40%;
  height:70%;
  display: flex;
  margin:0px;
  padding: 0px;
  flex-direction: column;
  justify-content:flex-start;
  align-items: flex-start;
  text-align: left;
}
.heroLeft p{
  text-align: left;
  width: 100%;
  flex-wrap: wrap;
  box-sizing: border-box;
  height:auto;
  font-size: 15px;
  line-height: 130%;
}
#copywrite{
  font-size: 70px;
  font-weight: bold;
  color: var(--primary1);
  margin:0px;
  padding:0px;
  line-height: 109%;
  position:absolute;

}
#end{
  font-size: 60px;
  font-weight: bold;
  margin-top:0.5rem;
  margin-bottom: 2rem;
  padding:0px;
  line-height: 115%;

}
.switch{
  width:100%;
  height: 15%;
  margin:0px;
  padding:0px;
}
.CTA{
  all:unset;
  padding: 0.7rem;
  margin-top: 0.5rem;
  background-color: var(--primary2-dark2);
  border-radius: 5px;
  color:white;
  font-weight: bold;
}
.CTA:hover{
  cursor: pointer;
  background-color: var(--primary2-dark1);
}
.heroRight{
  width: 40%;
  height:60%;
  box-sizing: border-box;
  position:relative;
  top:0%;
}
.heroRight img{
  position:absolute;
  margin:0px;
  padding:0px;
  right:0%;
  top:-50%;
  max-width: 130%;
  height:auto;
}

/*BENEFIT*=================================*/

.divide{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height:5rem;
  margin-bottom: 5rem;
  position: relative;
}
.divide h2{
  font-size: 20px;
  letter-spacing:8px;
}
.benefit{
  box-sizing: border-box;
  width:100%;
  padding: 0px 100px;
  display: flex;
  height: max-content;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  flex-direction: column;
  margin:0px;
}
.xp{
  position: relative;
  width:100%;
  height:700px;
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 170px;
  box-sizing: border-box;

}
.xp .stick{
  position:absolute;
  top:0%;
}
.xp .left{
  left:-7.5%;
}
.xp .right{
  right:-7.5%;
}
.xp .phone{
  height:80%;
}


.solutionText{
  width:50%;
  text-align: left;
  height:40%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.solutionText h2{
  font-size: 40px;
}
.solutionText p{
  font-size: 20px;
  line-height: 150%;
}

.xx{
width:350px;
height:32px;
margin:50px 0px;
}
.lastCTA{
  width:100%;
  height:400px;
  margin:0px;
  box-sizing: border-box;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position:relative;

}
.lastCTA h3{
  margin-top: 0px;
}

.sparker{
  position: absolute;
  width:40%;
  top:12%;
}

#test{
  width: 380px;
  margin: 30px;
  height: auto;
}
.warning{
  visibility: hidden;
}

.tagline{
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width:85%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.tagline-left{
  display: flex;
  width: 40vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 30px;
}
.tagline-right{
  width: 40vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
/*FOOTER ======================================*/

footer{
  padding:0px;
  width:100%;
  height: 150px;
  position:relative;
  bottom: 0px;
  background-color: var(--primary1);
  box-sizing: border-box;
}

.footer-1{
  width:80%;
  height:100%;
  margin:auto;
  padding: 0;
}
.footer-title{
  margin:auto;
  width:100%;
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}

.footer-right{
  display:flex;
  padding:2.5% 0px;
  width:20%;
  height:60%;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  margin: 0;
}
.footer-right p{
  padding: 0;
  margin: 0;
}
.sns-logo{
  padding: 0px;
  margin: 0px;
}
.footer-right a{
  text-decoration: none;
  color: black;
  opacity:1;
  transition: 0.1s ease-in-out;
  border-radius: 600px;
  margin-left: 20px;
}
.footer-right a:hover, .fa-brands:hover{
  color: var(--primary2-dark2);
  cursor: pointer;
  transition: 0.1s ease-in-out;
  opacity:1;
}
.footer-content{
  width:90%;
  display: flex;
  flex-direction: row;
  margin:0px;
}
.footer-about{
  width:50%;
  height: 100%;

}
.footer-signup{
  width:50%;
  height: 100%;
}
.footer-signup h3{
  width: 90%;
  margin:auto;
}
footer form{
  display:flex;
  flex-direction: column;
  width: 80%;
  margin:auto;
}
.footer-text{
  font-size: 10px;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: black;
  text-decoration: none;
  
}

.footer-text a{
  font-size: 10px;
  text-decoration: none;
  color: black;
}






/*MEDIA QUERY */
@media (max-width:525px){
  
  .wepo-logo{
    margin:0px;
    width: 3rem;
  }
  .nav{
    height: 40px;
    position: sticky;
    top:0px;
    z-index: 100;
    padding: 0px 20px;
    display: flex;

  }
  .icon{
    font-size: 1.5rem;
    color:black;
    display: unset;
  }
  .navRight{
    background: black;
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
    box-sizing: border-box;
    width:20px;
  }
  .heroContainer{
    all:unset;
    display: flex;
    justify-content: center;
    align-items: center;
    width:50%;
    box-sizing: border-box;
  }
  .footer{
    position:absolute;
    bottom: 0px;
    box-sizing: border-box;
  }
  
}